@import "default";
.body-hidden {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative; }
.container {
    background: #272727;
    width: 100%; }

.container__banner {
    position: relative;
    @include rwd(640px) {
        padding-bottom: 50vw; }
    @include rwd(480px) {
        padding-bottom: 80vw; }
    @include rwd(400px) {
        padding-bottom: 100vw; }
    @include rwd(360px) {
        padding-bottom: 140vw; } }
.width-wrap {
    width: $width-wrap;
    height: 100%;
    position: absolute;
    top: 0;
    @include calc('left','50% - 600px');
    z-index: 99;
    @include rwd(1200px) {
        width: 96%;
        @include calc('left','50% - 48%'); }
    @include rwd(480px) {
        padding-top: 30px;
        display: flex;
        flex-wrap: wrap; }
    .width-wrap__login {
        @include center(y);
        right: 0;
        padding: 30px 15px;
        width: 320px;
        background-color: rgba(255,255,255,0.2);
        border: 1px transparent solid;
        -moz-border-image: -moz-linear-gradient(to left, #d9c38f 40% , #f4e7c8 60%);
        -webkit-border-image: -webkit-linear-gradient(to left, #d9c38f 40% , #f4e7c8 60%);
        border-image: linear-gradient(to left, #d9c38f 40% , #f4e7c8 60%);
        -webkit-border-image-slice: 2;
        border-image-slice: 2;
        box-sizing: $box-sizing;
        @include rwd(1200px) {
            padding: 10px;
            width: 260px;
            right: 6vw; }
        @include rwd(640px) {
            background-color: rgba(0,0,0,0.5);
            width: 90%;
            right: inherit;
            @include center(x);
            top: 30px; }
        @include rwd(480px) {
            margin-bottom: 50px;
            order: 1;
            position: relative;
            top: 0; } }
    .width-wrap__qrcodebox {
        position: absolute;
        top: 20%;
        left: 0;
        max-width: 250px;
        @include rwd(1200px) {
            left: 6vw;
            max-width: 200px; }
        @include rwd(640px) {
            @include center(x);
            bottom: -8vw;
            top: auto; }
        @include rwd(480px) {
            order: 2;
            position: relative;
            top: 0; } } }

.login-logo {
    width: 130px;
    margin: auto;
    img {
        width: 100%; } }

.form {
    margin-top: 30px;
    @include rwd(1400px) {
        margin-top: 10px; }
    .form__field {
        padding: 6px 15px;
        &:after {
            clear: both;
            content: "";
            display: block;
            height: 0;
            line-height: 0;
            visibility: hidden; } }
    .form__label {
        text-align: right;
        margin-bottom: 3px;
        padding: 15px 0; }
    .form__label--hide {
	    display: none; }
    .form__control {
        width: 100%;
        padding: 10px 10px 10px 40px;
        box-sizing: $box-sizing;
        border: 1px solid $text-color2;
        border-width: 0 0 1px 0;
        background: none;
        color: $text-color;
        &::-webkit-input-placeholder {
            color: rgba(255,255,255,0.5); }
        &::-moz-placeholder {
            color: rgba(255,255,255,0.5); }
        &:-ms-input-placeholder {
            color: rgba(255,255,255,0.5); }
        &:-moz-placeholder {
            color: rgba(255,255,255,0.5); } }
    .form__inputgroup {
        position: relative;
        .form-icon {
            margin-right: 10px;
            color: $text-color2;
            @include font-size(24px);
            position: absolute;
            left: 5px;
            top: -5px; } } }

.btn {
    padding: 10px;
    width: 96%;
    margin: 10px 2% 0;
    cursor: pointer;
    @include font-size(16px);
    color: #5a4415;
    background: rgb(234,224,188);
    background: -moz-linear-gradient(bottom, rgba(234,224,188,1) 0%, rgba(222,194,132,1) 27%, rgba(222,191,122,1) 52%, rgba(213,182,109,1) 92%, rgba(234,224,188,1) 100%);
    background: -webkit-linear-gradient(bottom, rgba(234,224,188,1) 0%,rgba(222,194,132,1) 27%,rgba(222,191,122,1) 52%,rgba(213,182,109,1) 92%,rgba(234,224,188,1) 100%);
    background: linear-gradient(to top, rgba(234,224,188,1) 0%,rgba(222,194,132,1) 27%,rgba(222,191,122,1) 52%,rgba(213,182,109,1) 92%,rgba(234,224,188,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eae0bc', endColorstr='#eae0bc',GradientType=0 );
    border: 1px #aa822d solid;
    border-radius: 4px;
    box-shadow: inset 0 1px 0 rgba(255,255,255,1);
    box-sizing: $box-sizing;
    transition: all .5s ease-in-out;
    @include rwd2(980px) {
        &:hover {
            position: relative;
            left: 1px;
            top: 1px; } }
    .btn__icon {
        margin-right: 15px;
        transition: all 0.3s ease-in-out; } }

.btn--mt {
    margin-top: 30px; }

.qrcode {
    width: 100%;
    text-align: center;
    color: $text-color;
    margin-bottom: 15px;
    p {
        max-width: 75%;
        margin: auto;
        text-align: left;
        @include font-size(13px); }
    img {
        margin-top: 10px;
        max-width: 75%;
        height: auto;
        transition: all .5s ease-in-out; } }
.flex {
    li {
        @include calc('width','25% - 10px');
        margin: 0 5px;
        img {
            width: 100%;
            height: 55px;
            @include rwd2(980px) {
                &:hover {
                    position: relative;
                    left: 1px;
                    top: 1px; } } } } }


// step
.container__step {
    width: 90%;
    padding: 5rem 5rem 3rem;
    box-sizing: $box-sizing;
    margin: auto;
    @include rwd(1440px) {
        width: 97%;
        padding: 5rem 2.5rem 3rem; }
    @include rwd(480px) {
        padding: 5rem 20px 3rem; } }
.step {
    @include calc('width','33.333333333% - 30px');
    margin: 0 15px 30px;
    box-sizing: $box-sizing;
    padding: 0 1.8rem 1.8rem;
    @include bg("step_bg.jpg",left,top,repeat);
    color: $text-color;
    @include font-size(20px);
    position: relative;
    min-height: 280px;
    &:after {
        position: absolute;
        right: 1.8rem;
        bottom: 0;
        content: "";
        width: 30%;
        height: 1px;
        background: $text-color2; }
    @include rwd(1440px) {
        @include calc('width','50% - 30px'); }
    @include rwd(768px) {
        @include calc('width','100% - 30px'); }
    @include rwd(480px) {
        width: 100%;
        margin: 0 0 30px 0; } }
.step__dt {
    margin-bottom: 5px;
    position: relative;
    @include font-size(28px);
    &:after {
        position: absolute;
        right: 0;
        top: 30px;
        content: "";
        @include bg("step-icon-color.svg",right,top,no-repeat);
        background-size: 50px;
        width: 50px;
        height: 50px; }
    .num {
        @include font-size(30px);
        font-style: oblique;
        font-family: $text-family2;
        position: relative;
        padding: 0 2.5rem 0 0;
        width: 72px;
        box-sizing: $box-sizing;
        &:after {
            position: absolute;
            right: 20px;
            top: 30px;
            content: "";
            width: 1px;
            height: 50px;
            background: $text-color;
            transform: rotate(45deg); } }
    .steptitle {
        margin-top: 10px;
        @include calc('width','100% - 72px');
        padding: 15px 60px 0 10px;
        line-height: 2rem;
        box-sizing: $box-sizing; }
    @include rwd(480px) {
        &:after {
            background-size: 35px; } } }
.step__dt--sm {
    .steptitle {
        font-size: 26px;
        line-height: 2rem;
        margin-bottom: 10px; } }

.step__dd {
    @include font-size(20px);
    font-family: $text-family2;
    font-style: oblique; }


// contact
.container__contact {
    width: 100%; }

.contact {
    background-color: #292929;
    .contact__bg {
        flex: 1;
        @include bg("contact-bg.jpg",center,center,no-repeat);
        background-size: cover;
        @include rwd(640px) {
            display: none; } }
    .contact__info {
        flex: 1;
        color: $text-color;
        padding: 4rem 0;
        min-height: 380px;
        box-sizing: $box-sizing;
        background: -moz-linear-gradient(bottom, rgba(222,194,132,0.7) 0%, rgba(222,191,122,0.61) 22%, rgba(130,115,84,0.33) 92%, rgba(130,115,84,0.3) 100%);
        background: -webkit-linear-gradient(bottom, rgba(222,194,132,0.7) 0%,rgba(222,191,122,0.61) 22%,rgba(130,115,84,0.33) 92%,rgba(130,115,84,0.3) 100%);
        background: linear-gradient(to top, rgba(222,194,132,0.7) 0%,rgba(222,191,122,0.61) 22%,rgba(130,115,84,0.33) 92%,rgba(130,115,84,0.3) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b3dec284', endColorstr='#4d827354',GradientType=0 );
        dt {
            @include font-size(28px);
            font-family: $text-family2;
            margin: 0 auto 20px;
            width: 90%; }
        dd {
            @include font-size(16px);
            font-family: $text-family;
            margin: 0 auto 15px;
            width: 90%;
            a {
                color: $text-color;
                &:hover {
                    color: $hover-color; } } } } }

.container__ft {
    padding: 20px;
    text-align: center;
    font-family: $text-family;
    @include font-size(13px);
    a {
        color: $text-color;
        &:hover {
            color: $hover-color; } } }

/*logo action*/
.logo-action {
    width: 100%;
    height: 100vh;
    background-color: #272727;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100; }
.action-box {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center; }
.logo {
    width: 100%;
    max-width: 500px;
    overflow: visible; }
