//This is sass file
body {
	font-family: $text-family;
	color: $text-color;
	line-height: $line-height;
	font-size: $fontSize;
	transition: all .3s ease-out; }

.hide {
	display: none; }
.right {
	float: right; }
.left {
	float: left; }
.wrapper {
	width: 100%;
	max-width: $width-wrap;
	margin: 0 auto; }
.img {
	display: block; }
.flex {
	display: flex;
	flex-wrap: wrap; }
