@charset "UTF-8";
body {
  font-family: "Open Sans", "微軟正黑體", Verdana, Geneva, sans-serif;
  color: #fff;
  line-height: 24px;
  font-size: 16px;
  transition: all .3s ease-out;
}

.hide {
  display: none;
}

.right {
  float: right;
}

.left {
  float: left;
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.img {
  display: block;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.body-hidden {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.container {
  background: #272727;
  width: 100%;
}

.container__banner {
  position: relative;
}

@media only screen and (max-width: 640px) {
  .container__banner {
    padding-bottom: 50vw;
  }
}

@media only screen and (max-width: 480px) {
  .container__banner {
    padding-bottom: 80vw;
  }
}

@media only screen and (max-width: 400px) {
  .container__banner {
    padding-bottom: 100vw;
  }
}

@media only screen and (max-width: 360px) {
  .container__banner {
    padding-bottom: 140vw;
  }
}

.width-wrap {
  width: 1200px;
  height: 100%;
  position: absolute;
  top: 0;
  left: -moz-calc(50% - 600px);
  left: -webkit-calc(50% - 600px);
  left: calc(50% - 600px);
  z-index: 99;
}

@media only screen and (max-width: 1200px) {
  .width-wrap {
    width: 96%;
    left: -moz-calc(50% - 48%);
    left: -webkit-calc(50% - 48%);
    left: calc(50% - 48%);
  }
}

@media only screen and (max-width: 480px) {
  .width-wrap {
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
  }
}

.width-wrap .width-wrap__login {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  padding: 30px 15px;
  width: 320px;
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px transparent solid;
  -moz-border-image: -moz-linear-gradient(to left, #d9c38f 40%, #f4e7c8 60%);
  -webkit-border-image: -webkit-linear-gradient(to left, #d9c38f 40%, #f4e7c8 60%);
  border-image: linear-gradient(to left, #d9c38f 40%, #f4e7c8 60%);
  -webkit-border-image-slice: 2;
  border-image-slice: 2;
  box-sizing: border-box;
}

@media only screen and (max-width: 1200px) {
  .width-wrap .width-wrap__login {
    padding: 10px;
    width: 260px;
    right: 6vw;
  }
}

@media only screen and (max-width: 640px) {
  .width-wrap .width-wrap__login {
    background-color: rgba(0, 0, 0, 0.5);
    width: 90%;
    right: inherit;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 30px;
  }
}

@media only screen and (max-width: 480px) {
  .width-wrap .width-wrap__login {
    margin-bottom: 50px;
    order: 1;
    position: relative;
    top: 0;
  }
}

.width-wrap .width-wrap__qrcodebox {
  position: absolute;
  top: 20%;
  left: 0;
  max-width: 250px;
}

@media only screen and (max-width: 1200px) {
  .width-wrap .width-wrap__qrcodebox {
    left: 6vw;
    max-width: 200px;
  }
}

@media only screen and (max-width: 640px) {
  .width-wrap .width-wrap__qrcodebox {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: -8vw;
    top: auto;
  }
}

@media only screen and (max-width: 480px) {
  .width-wrap .width-wrap__qrcodebox {
    order: 2;
    position: relative;
    top: 0;
  }
}

.login-logo {
  width: 130px;
  margin: auto;
}

.login-logo img {
  width: 100%;
}

.form {
  margin-top: 30px;
}

@media only screen and (max-width: 1400px) {
  .form {
    margin-top: 10px;
  }
}

.form .form__field {
  padding: 6px 15px;
}

.form .form__field:after {
  clear: both;
  content: "";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
}

.form .form__label {
  text-align: right;
  margin-bottom: 3px;
  padding: 15px 0;
}

.form .form__label--hide {
  display: none;
}

.form .form__control {
  width: 100%;
  padding: 10px 10px 10px 40px;
  box-sizing: border-box;
  border: 1px solid #d7c39b;
  border-width: 0 0 1px 0;
  background: none;
  color: #fff;
}

.form .form__control::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form .form__control::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form .form__control:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form .form__control:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form .form__inputgroup {
  position: relative;
}

.form .form__inputgroup .form-icon {
  margin-right: 10px;
  color: #d7c39b;
  font-size: 24px;
  font-size: 1.5;
  line-height: 2.25;
  position: absolute;
  left: 5px;
  top: -5px;
}

.btn {
  padding: 10px;
  width: 96%;
  margin: 10px 2% 0;
  cursor: pointer;
  font-size: 16px;
  font-size: 1;
  line-height: 1.5;
  color: #5a4415;
  background: #eae0bc;
  background: -moz-linear-gradient(bottom, #eae0bc 0%, #dec284 27%, #debf7a 52%, #d5b66d 92%, #eae0bc 100%);
  background: -webkit-linear-gradient(bottom, #eae0bc 0%, #dec284 27%, #debf7a 52%, #d5b66d 92%, #eae0bc 100%);
  background: linear-gradient(to top, #eae0bc 0%, #dec284 27%, #debf7a 52%, #d5b66d 92%, #eae0bc 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eae0bc', endColorstr='#eae0bc',GradientType=0 );
  border: 1px #aa822d solid;
  border-radius: 4px;
  box-shadow: inset 0 1px 0 white;
  box-sizing: border-box;
  transition: all .5s ease-in-out;
}

@media only screen and (min-width: 980px) {
  .btn:hover {
    position: relative;
    left: 1px;
    top: 1px;
  }
}

.btn .btn__icon {
  margin-right: 15px;
  transition: all 0.3s ease-in-out;
}

.btn--mt {
  margin-top: 30px;
}

.qrcode {
  width: 100%;
  text-align: center;
  color: #fff;
  margin-bottom: 15px;
}

.qrcode p {
  max-width: 75%;
  margin: auto;
  text-align: left;
  font-size: 13px;
  font-size: 0.8125;
  line-height: 1.21875;
}

.qrcode img {
  margin-top: 10px;
  max-width: 75%;
  height: auto;
  transition: all .5s ease-in-out;
}

.flex li {
  width: -moz-calc(25% - 10px);
  width: -webkit-calc(25% - 10px);
  width: calc(25% - 10px);
  margin: 0 5px;
}

.flex li img {
  width: 100%;
  height: 55px;
}

@media only screen and (min-width: 980px) {
  .flex li img:hover {
    position: relative;
    left: 1px;
    top: 1px;
  }
}

.container__step {
  width: 90%;
  padding: 5rem 5rem 3rem;
  box-sizing: border-box;
  margin: auto;
}

@media only screen and (max-width: 1440px) {
  .container__step {
    width: 97%;
    padding: 5rem 2.5rem 3rem;
  }
}

@media only screen and (max-width: 480px) {
  .container__step {
    padding: 5rem 20px 3rem;
  }
}

.step {
  width: -moz-calc(33.333333333% - 30px);
  width: -webkit-calc(33.333333333% - 30px);
  width: calc(33.333333333% - 30px);
  margin: 0 15px 30px;
  box-sizing: border-box;
  padding: 0 1.8rem 1.8rem;
  background-image: url("../images/step_bg.jpg");
  background-position: left top;
  background-repeat: repeat;
  color: #fff;
  font-size: 20px;
  font-size: 1.25;
  line-height: 1.875;
  position: relative;
  min-height: 280px;
}

.step:after {
  position: absolute;
  right: 1.8rem;
  bottom: 0;
  content: "";
  width: 30%;
  height: 1px;
  background: #d7c39b;
}

@media only screen and (max-width: 1440px) {
  .step {
    width: -moz-calc(50% - 30px);
    width: -webkit-calc(50% - 30px);
    width: calc(50% - 30px);
  }
}

@media only screen and (max-width: 768px) {
  .step {
    width: -moz-calc(100% - 30px);
    width: -webkit-calc(100% - 30px);
    width: calc(100% - 30px);
  }
}

@media only screen and (max-width: 480px) {
  .step {
    width: 100%;
    margin: 0 0 30px 0;
  }
}

.step__dt {
  margin-bottom: 5px;
  position: relative;
  font-size: 28px;
  font-size: 1.75;
  line-height: 2.625;
}

.step__dt:after {
  position: absolute;
  right: 0;
  top: 30px;
  content: "";
  background-image: url("../images/step-icon-color.svg");
  background-position: right top;
  background-repeat: no-repeat;
  background-size: 50px;
  width: 50px;
  height: 50px;
}

.step__dt .num {
  font-size: 30px;
  font-size: 1.875;
  line-height: 2.8125;
  font-style: oblique;
  font-family: "Amiri", "微軟正黑體", Verdana, Geneva, serif;
  position: relative;
  padding: 0 2.5rem 0 0;
  width: 72px;
  box-sizing: border-box;
}

.step__dt .num:after {
  position: absolute;
  right: 20px;
  top: 30px;
  content: "";
  width: 1px;
  height: 50px;
  background: #fff;
  transform: rotate(45deg);
}

.step__dt .steptitle {
  margin-top: 10px;
  width: -moz-calc(100% - 72px);
  width: -webkit-calc(100% - 72px);
  width: calc(100% - 72px);
  padding: 15px 60px 0 10px;
  line-height: 2rem;
  box-sizing: border-box;
}

@media only screen and (max-width: 480px) {
  .step__dt:after {
    background-size: 35px;
  }
}

.step__dt--sm .steptitle {
  font-size: 26px;
  line-height: 2rem;
  margin-bottom: 10px;
}

.step__dd {
  font-size: 20px;
  font-size: 1.25;
  line-height: 1.875;
  font-family: "Amiri", "微軟正黑體", Verdana, Geneva, serif;
  font-style: oblique;
}

.container__contact {
  width: 100%;
}

.contact {
  background-color: #292929;
}

.contact .contact__bg {
  flex: 1;
  background-image: url("../images/contact-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media only screen and (max-width: 640px) {
  .contact .contact__bg {
    display: none;
  }
}

.contact .contact__info {
  flex: 1;
  color: #fff;
  padding: 4rem 0;
  min-height: 380px;
  box-sizing: border-box;
  background: -moz-linear-gradient(bottom, rgba(222, 194, 132, 0.7) 0%, rgba(222, 191, 122, 0.61) 22%, rgba(130, 115, 84, 0.33) 92%, rgba(130, 115, 84, 0.3) 100%);
  background: -webkit-linear-gradient(bottom, rgba(222, 194, 132, 0.7) 0%, rgba(222, 191, 122, 0.61) 22%, rgba(130, 115, 84, 0.33) 92%, rgba(130, 115, 84, 0.3) 100%);
  background: linear-gradient(to top, rgba(222, 194, 132, 0.7) 0%, rgba(222, 191, 122, 0.61) 22%, rgba(130, 115, 84, 0.33) 92%, rgba(130, 115, 84, 0.3) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b3dec284', endColorstr='#4d827354',GradientType=0 );
}

.contact .contact__info dt {
  font-size: 28px;
  font-size: 1.75;
  line-height: 2.625;
  font-family: "Amiri", "微軟正黑體", Verdana, Geneva, serif;
  margin: 0 auto 20px;
  width: 90%;
}

.contact .contact__info dd {
  font-size: 16px;
  font-size: 1;
  line-height: 1.5;
  font-family: "Open Sans", "微軟正黑體", Verdana, Geneva, sans-serif;
  margin: 0 auto 15px;
  width: 90%;
}

.contact .contact__info dd a {
  color: #fff;
}

.contact .contact__info dd a:hover {
  color: #d7c39b;
}

.container__ft {
  padding: 20px;
  text-align: center;
  font-family: "Open Sans", "微軟正黑體", Verdana, Geneva, sans-serif;
  font-size: 13px;
  font-size: 0.8125;
  line-height: 1.21875;
}

.container__ft a {
  color: #fff;
}

.container__ft a:hover {
  color: #d7c39b;
}

/*logo action*/
.logo-action {
  width: 100%;
  height: 100vh;
  background-color: #272727;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.action-box {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.logo {
  width: 100%;
  max-width: 500px;
  overflow: visible;
}
